<template>
	<view class="container">
		<image src="http://localhost:3000/public/images/bj/bg1.png" mode="widthFix" class="bg1"></image>
		<image src="http://localhost:3000/public/images/bj/bg2.png" mode="widthFix" class="bg2"></image>
		<view class="form-box">
			<view class="title">用户注册</view>
			<uni-forms ref="registerForm" :rules="rules" :modelValue="registerForm">
				<uni-forms-item name='username' class="input-box">
					<input type="text" label-width='' placeholder="请输入用户名" class="input" v-model="registerForm.username">
				</uni-forms-item>
				<uni-forms-item name='email' class="input-box">
					<input type="text" label-width='' placeholder="请输入邮箱" class="input" v-model="registerForm.email">
				</uni-forms-item>
				<uni-forms-item name='password' class="input-box">
					<input class="input" placeholder="请输入密码" :password="showPassword" v-model="registerForm.password" />
					<uni-icons :type="showPassword?'eye-filled':'eye-slash-filled'" :color="showPassword?'#009000':'#999'" size="25" class="icon" @click="changePassword(1)" v-show="registerForm.password"></uni-icons>
				</uni-forms-item>
				<uni-forms-item name='password_confirmation' class="input-box">
					<input class="input" placeholder="请输入确认密码" :password="showPasswordConfirmation" v-model="registerForm.password_confirmation" />
					<uni-icons :type="showPasswordConfirmation?'eye-filled':'eye-slash-filled'" :color="showPasswordConfirmation?'#009000':'#999'" size="25" class="icon" @click="changePassword(2)" v-show="registerForm.password_confirmation"></uni-icons>
				</uni-forms-item>
			</uni-forms>
			<button class="btn" @click="clickRegister">注册</button>
			<view class="tip2" @click="toLogin">已有账号？<text>点击登录！</text></view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				registerForm: {
					username: '',
					email:'',
					password: '',
					password_confirmation:''
				},
				rules: { // 校验规则
					username: {
						rules: [{
							required: true,
							errorMessage: '用户名不能为空'
						}]
					},
					email: {
						rules: [{
							required: true,
							errorMessage: '邮箱不能为空'
						},{
							pattern:'^\\S+?@\\S+?\\.\\S+?$',
							errorMessage:'邮箱格式错误'
						}]
					},
					password: {
						rules: [{
							required: true,
							errorMessage: '密码不能为空'
						}]
					},
					password_confirmation: {
						rules: [{
							required: true,
							errorMessage: '确认密码不能为空'
						}]
					}
				},
				showPassword: true,
				showPasswordConfirmation:true
			}
		},
		watch:{
			'registerForm.password':function(newVal,oldVal){
				if(!newVal){
					this.showPassword=true
				}
			},
			'registerForm.password_confirmation':function(newVal,oldVal){
				if(!newVal){
					this.showPasswordConfirmation=true
				}
			}
		},
		methods: {
			changePassword: function(id) {
				if(id==1){
					this.showPassword = !this.showPassword;
				}else{
					this.showPasswordConfirmation = !this.showPasswordConfirmation;
				}
			},
			clickRegister() {
				this.$refs.registerForm.validate().then(res => { //效验注册信息
				    let password = this.registerForm.password
					let passwordConfirmation = this.registerForm.password_confirmation
				    if(password==passwordConfirmation){
						this.submitRegister()
					}else{
						uni.showToast({
							title: '密码不一致',
							icon: 'error',
							duration: 2000
						});
					}
				}).catch(err => {
					console.log('err', err);
				})
			},
			async submitRegister() { //发送注册请求
			    let registerForm = (({username,email,password}) => ({username,email,password}))(this.registerForm)
				const response = await this.$api.user.register(registerForm)
				if (response.data.code === 200) { //登录成功
					uni.showToast({
						title: response.data.message,
						icon: 'none',
						duration: 2000
					});
					// 页面跳转
					let t = setTimeout(()=>{
						this.toLogin()
						clearTimeout(t)
					},1000)
				} 
			},
			toLogin(){ //跳转登录页
				uni.redirectTo({
					url:'/pages/user/login'
				})
			}
		}
	}
</script>

<style lang="scss">
	.container {
		width: 100%;
		min-height: 100vh;
		background-color: #fff;
		position: relative;

		.bg1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			z-index: 1;
		}

		.bg2 {
			position: absolute;
			bottom: 100upx;
			left: calc(50% - 170upx);
			width: 350upx;
			z-index: 1;
		}

		.form-box {
			position: absolute;
			top: calc(50% - 330upx);
			left: 0;
			z-index: 2;
			width: 100%;
			padding: 0 50upx;
			text-align: center;

			.title {
				font-size: 50upx;
				color: #009000;
				font-weight: bold;
				display: inline-block;
				margin-bottom: 20upx;
			}

			.input-box {
				width: 100%;
				margin-top: 40upx;
                position: relative;
				.input {
					width: 100%;
					height: 80upx;
					border: 1px solid #009000;
					border-radius: 8upx;
					text-align: left;
					padding: 0 30upx;
					font-size: 28upx;
				}
				.icon{
					position: absolute;
					top: calc(50% - 20upx);
					right: 20upx;
				}
			}

			.btn {
				width: 100%;
				height: 80upx;
				line-height: 80upx;
				background-color: #009000;
				border-radius: 50upx;
				color: #fff;
				margin-top: 50upx;
				font-size: 35upx;
			}
			.tip2 {
				font-size: 26upx;
				color: $uni-secondary-color;
				position: fixed;
				bottom: 15upx;
				left: 0;
				width: 100%;
				padding: 20upx 0;

				text {
					color: #009000;
				}
			}
		}
	}
</style>